:root {
  --primary-accent-color: #bbf330;
  --secondary-accent-color: #7eb101;
  --accent-color: #ffe550;
  --shadow-fruit: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --primary-white-color: #fefefe;
  --secondary-white-color: #fff;
  --primary-text-color: #2c2c2c;
  --transition-dur-and-func: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.logo {
  line-height: 0;
  display: block;
}

.site-nav {
  display: none;
}

@media screen and (min-width: 768px) {
