.backdrop {
  width: 100%;
  height: 100%;
  z-index: 100;
  transition: opacity var(--transition-dur-and-func), visibility var(--transition-dur-and-func);
  background-color: rgba(94, 99, 99, .7);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 100%;
  background: var(--accent-color);
  border-radius: 30px;
  padding: 32px 20px;
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.modal-close-btn {
  width: 32px;
  height: 32px;
  background: var(--secondary-white-color);
  border: none;
  border-radius: 50%;
  padding: 0;
  line-height: 0;
  display: block;
  position: absolute;
  top: 12px;
  right: 12px;
